export default {
	data() {
		return {
			endScrollTop:0,
			// 顶部小导航栏显示隐藏导航栏布尔值
			navBoo:true,
			// 顶部位置显示与隐藏布尔值
			posBoo:false,
			// 中部与scroll滚动块上面盒子的高度->擦除效果
			BCHeight:100,
			// 中部与scroll滚动块上面盒子的孩子的高度->擦除效果
			CDHeight:70,
			// 控制大导航显隐参数
			CDBooShowHide:true,
			// 大导航上边距的动态参数
			DCMarginTop:0,
			// 增加向上擦除的参数
			addMarginTop:0,
			query:null,
			scrollDom:null
		}
	},
	mounted(){
	},
	methods: {
		// 触摸开始的回调函数
		scrollTouchStart(){
			this.startTime = new Date();
		},
		// 触摸结束时回调函数
		scrollTouchEnd(){
			let theTime = new Date() - this.startTime;
			// console.log(theTime);
			console.log(this.endScrollTop);
			const endScrollTop = this.endScrollTop;
			if(endScrollTop>=0&&endScrollTop<=40){
				uni.pageScrollTo({
					duration:200,
					scrollTop:0
				})
			}else if(endScrollTop>40&&endScrollTop<=98){
				uni.pageScrollTo({
					duration:200,
					scrollTop:98
				})
			}
		},
		// 大导航点击测试
		navClick(){
			console.log('你点击了大导航');
		}
	},
	onPageScroll(o){
		let scrollTop = o.scrollTop;
		this.endScrollTop = scrollTop;
		// 控制大导航显隐
		if(scrollTop >= 40){
			this.CDBooShowHide = false;
		}else{
			this.CDBooShowHide = true;
		}
		// 大导航移动效果+加快擦除
		if(scrollTop >= 25){
			this.DCMarginTop = 25 - scrollTop;
			this.addMarginTop = -this.DCMarginTop;
		}else{
			this.addMarginTop = this.DCMarginTop = 0;
		}
		// 擦除效果
		this.CDHeight = 70 - scrollTop - this.addMarginTop;
	}
}
